.p-login {
  &__account {
    background: $c-primary;
    color: $c-secondary;
    width: 50%;
    float: left;
    @include breakpoint(mb) {
      width: 100%;
      margin-bottom: -30px; }
    &__comunity {
      display: none;
      background: $c-secondary;
      padding-top: 30px;
      padding-bottom: 30px;
      padding-left: 9%;
      padding-right: 9%;
      margin-bottom: 30px;
      @include clearfix;
      li {
        float: left;
        width: 33.33%;
        a {
          display: block;
          height: 56px;
          border: 1px solid $c-secondary;
          background: white;
          &:hover {
            background: $c-community-bg; }
          img {
            margin-top: 8px;
            height: 20px;
            width: auto; } } }
      @include breakpoint(mb) {
        display: block; } }
    h2 {
      font-size: 2.25rem;
      line-height: 50px;
      margin-top: 40px;
      margin-bottom: 40px;
      text-align: center; }
    .form-text {
      height: 56px;
      width: 82%;
      margin-left: 9%;
      margin-bottom: 16px;
      background: $c-secondary;
      i {
        color: $c-primary;
        vertical-align: middle;
        margin-left: 20px;
        margin-right: 20px; } }
    [type="text"] {
      margin-top: 18px;
      border-width: 0;
      width: calc(100% - 75px);
      background: $c-secondary;
      &::placeholder {
        color: $c-info; } }
    [type="checkbox"] {
      margin-left: 9%;
      margin-bottom: 22px;
      vertical-align: sub; }
    a {
      display: block;
      color: $c-primary;
      line-height: 56px;
      width: 100%;
      font-size: 1.5rem;
      text-align: center;
      background: $c-warning;
      border-width: 0;
      &:hover {
        background: $c-warning-comp; } } }
  &__comunity {
    background: $c-secondary;
    width: 50%;
    float: left;
    height: 350px;
    margin-top: 10px;
    @include breakpoint(mb) {
      display: none; }
    h2 {
      font-size: 1.5rem;
      color: $c-info;
      margin-top: 30px;
      margin-bottom: 30px;
      text-align: center;
      font-weight: $weight-light; }
    li {
      margin-left: 9%;
      margin-bottom: 16px;
      a {
        height: 56px;
        width: 82%;
        background: white;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover,&:focus {
          background: $c-community-bg; }
        img {
          height: 30px; } } } } }



